<template>
    <div id="register">
      <div class="input_b clear">
        <div class="input_style">
          <span>用户：</span>
          <el-input v-model="username" placeholder="请输入用户名"></el-input>
          <div class="msg">要以字母开头,字母数字下划线,长度为4-16位</div>
        </div>
        <div class="input_style">
          <span>密码：</span>
          <el-input type="password" v-model="password1" placeholder="请输入密码"></el-input>
          <div class="msg">可以使用字母,数字,长度最小为6位</div>
        </div>
        <div class="input_style">
          <span>确认密码：</span>
          <el-input type="password" v-model="password2" placeholder="请再次输入密码"></el-input>

        </div>
      </div>
      <div class="btn_style">
        <el-button @click="onRegister">立即注册</el-button>
        <el-button @click="reset">重 置</el-button>
      </div>
    </div>
</template>

<script>
  import { mapActions } from 'vuex'
  export default {
      name: "Register",
      data(){
        return{
          username:'',
          password1:'',
          password2:''
        }
      },
      methods: {
        ...mapActions(['register']),
        onRegister(){
          if (/^[a-zA-Z]\w{3,15}$/.test(this.username)&&/^[a-zA-Z0-9]{6,}$/.test(this.password1)){
              if(this.password1 === this.password2){
                this.register({username:this.username,password:this.password1})
                  .then(()=>{
                    this.$message.success('注册成功!')
                    this.$router.push({path:'/'});
                  })
              }else{
                this.$message.error('两次输入的密码不同')
              }
          }else{
            this.$message.error('用户名或密码不符合要求')
          }
        },
        reset(){
          this.username = '';
          this.password1 = '';
          this.password2 = '';
        }
      }
    }
</script>

<style scoped>
  #register{
    width: 50%;
    margin: 40px auto;
    padding-top: 20px;
    font-size: 16px;
    min-width: 384px;
  }
  .input_style{
    padding: 10px 10px 0px 10px;
    float: right;
    /*text-align: center;*/
  }
  .input_b{
    width: 406px;
    margin:13px auto;
  }
  .btn_style{
    text-align: center;
  }
  .msg{
    color: #788c2b;
    font-size: 10px;
    padding-top: 5px;
    text-align: center;
  }
  .some_msg{
    font-size: 14px;
    margin-top: 70px;
    text-align: center;
  }
  .some_msg a{
    color:#788c2b;
  }
  .el-button{
    background-color: #e3edbb;
    border-color: #afc850;
    border-radius: 3px;
    color: #788c2b;
    padding: 8px 10px;
    margin: 25px 30px 0px 30px;
  }
  .el-button:hover{
    background-color:#afc850;
    color: #ffffff;
  }
  .el-input{
    width: 300px;
  }
  .clear::after{
    content: '';
    display: block;
    clear: both;
  }
</style>



